import { Link } from "react-router-dom";
import Container from "./Container";
import styles from './NavBar.module.css'
import logo from '../../img/costs_logo.png'


export default function NavBar({authLogin, setAuthLogin}){
    return(
       <div className={styles.navbar}>
           <Container>
                <Link to='/'><img src={logo} alt='costs'/></Link>
                <ul className={styles.list}>
                    <li className={styles.item}><Link to='/'>Home</Link></li>
                    <li className={styles.item}><Link to='/Projetos'>Projetos</Link></li>
                    <li className={styles.item}><Link to='/Sobre'>Sobre</Link></li>
                    <li className={styles.item}><Link to='/Contato'>Contato</Link></li>
                    {authLogin ?
                        <li className={styles.item} onClick={()=>{setAuthLogin(!authLogin)}}>
                            <Link to='/Login'>Logout</Link>
                        </li>
                    :
                        <li className={styles.item}>
                            <Link to="/Login">Login</Link>
                        </li>
                    }
                </ul>
            </Container>
        </div>
    )
}


