import Input from '../form/Input'
import Select from '../form/Select'
import SubmitBtn from '../form/SubmitBtn'
import styles from './ProjectForm.module.css'
import { useEffect, useState } from 'react'
import Message from '../layout/Message'

export default function Form({btnText, handleSubmit, projectData}){
    const [categories, setCategories] = useState([])
    const [project, setProject] = useState(projectData || {})
    const [showMsgError, SetShowMsgError] = useState(false)

    useEffect(()=>{
        fetch("http://localhost:5000/categories", {
            method: "GET",
            headers:{
                "Content-Type": "application/json"
            }
        })
        .then((res) => res.json())
        .then((data) => {
            setCategories(data)
        })
        .catch((err)=>console.log(err))
    }, [])


    const submit = (e)=>{
        
        if(project.name === undefined || project.category === undefined|| project.budget === undefined){
            SetShowMsgError(true)
            return false
        }
        e.preventDefault()
        handleSubmit(project)
    }

    function handleChange(e){
        setProject({...project, [e.target.name]:e.target.value })
    }

    function handleCategory(e){
        setProject({...project, category:{
            id: e.target.value,
            name: e.target.options[e.target.selectedIndex].text,
        },})
    }

    return(
        <>
        {!showMsgError && <Message msg={"preencha todos os campos"} type={"error"}/>}
        <form onSubmit={submit} className={styles.form}>
           <Input type='text' 
           text='Nome do projeto' 
           name='name' 
           placeholder='Insira o nome do projeto'
           handleOnChange={handleChange}
           value={project.name ? project.name : ''}/>

           <Input type='number' 
           text='Orçamento do projeto' 
           name='budget' 
           placeholder='Insira o valor do orçamento'
           handleOnChange={handleChange}
           value={project.budget ? project.budget : ''}/>
           
           <Select name='category_id' 
           text='Selecione a categoria' 
           options={categories}
           handleOnChange={handleCategory}
           value={project.category ? project.category.id : ''}/>

           <SubmitBtn text={btnText}/>
        </form>
        </>
    )
}