import styles from "./EditarProjeto.module.css"
import { useParams } from "react-router-dom"
import { useState, useEffect } from "react"
import Loading from "../layout/Loading"
import Container from "../layout/Container"
import ProjectForm from "../projects/ProjectForm"
import Message from "../layout/Message"
import ServiceForm from "../services/ServiceForm"

export default function EditarProjeto(){
    const { id } = useParams() //pega id da url
    const [project, setProject] = useState([])
    const [showProjectForm, setShowProjectForm] = useState(false)
    const [msg, setMsg] = useState()
    const [typeMsg, setTypeMsg]= useState()
    const [showServiceForm, setShowServiceForm] = useState(false)
    
    useEffect(()=>{
        fetch(`http://localhost:5000/projects/${id}`,{
            method: "GET",
            headers: {
                "Content-Type": "application/json"
            }
        }).then(res => res.json())
        .then((data) => {
            setProject(data)
        })
        .catch(err => console.log(err))
    }, [id])

    function toggleProjectForm(){
        setShowProjectForm(!showProjectForm)
    }

    function toggleServiceForm(){
        setShowServiceForm(!showServiceForm)
    }
    function editPost(project){
        setMsg('')
        if(project.budget < project.cost){
            setMsg("Error: Orçamento inferior ao custo do projeto.")
            setTypeMsg("error")
            return false
        }
        
        fetch(`http://localhost:5000/projects/${project.id}`, {
            method: "PATCH",
            headers: {
                "Content-Type": "application/json"
            },
            body: JSON.stringify(project),
        }).then(res => res.json())
        .then((data) => {
            setProject(data)
            setShowProjectForm(!showProjectForm)
            setMsg("Projeto atualizado com sucesso.")
            setTypeMsg("success")    
        })
        .catch(err => console.log(err))
    }

    function createService(){

    }

    return(
        <>
        {project.name ?
        <div className={styles.project_details}>
            <Container customClass='column'>
                {msg && <Message msg={msg} type={typeMsg}/>}
                <div className={styles.details_container}>
                    <h1>Projeto: {project.name}</h1>
                    <button className={styles.btn} onClick={toggleProjectForm}>
                       {!showProjectForm ? "Editar projeto" : "Fechar"}
                    </button>
                    {!showProjectForm ? 
                    <div className={styles.project_info}>
                        <p><span>Categoria:</span> {project.category.name}</p>
                        <p><span>Orçamento total:</span> R${project.budget}</p>
                        <p><span>Orçamento utilizado:</span> R${project.cost}</p>
                    </div> 
                    :
                    <div className={styles.project_info}>
                       <ProjectForm handleSubmit={editPost} btnText={'Confirmar'} projectData={project}/>
                    </div>
                    }
                </div>
                <div className={styles.service_form_container}>
                    <h2>Adicione um serviço:</h2>
                    <button className={styles.btn} onClick={toggleServiceForm}>
                       {!showServiceForm ? "Adicionar serviço" : "Fechar"}
                    </button>
                    <div className={styles.project_info}>
                        {showServiceForm && (
                        <ServiceForm handleSubmit={createService}
                        btnText="Adicionar serviço"
                        projectData={project}
                        />)}
                    </div>
                </div>
               <h2>Serviços</h2>
               <Container customClass='start'>
                   <p>itens dos servicos</p>
                </Container> 
            </Container>
        </div>
        :
            <Loading/>
        }
        </>
    )
}