import {useState} from 'react';
import {BrowserRouter as Router, Route, Routes} from 'react-router-dom'
import Contato from './components/pages/Contato';
import Home from './components/pages/Home';
import NovoProjeto from './components/pages/NovoProjeto';
import Sobre from './components/pages/Sobre';
import Projetos from './components/pages/Projetos';
import Login from './components/pages/Login'

import Container from './components/layout/Container';
import NavBar from './components/layout/NavBar';
import Footer from './components/layout/Footer';
import EditarProjeto from './components/pages/EditarProjeto';
import LoginMessage from './components/layout/LoginMessage';




export default function App() {
  const [authLogin, setAuthLogin] = useState(false)

  return (
    <Router>
      <NavBar authLogin={authLogin} setAuthLogin={setAuthLogin}/>
      <Container customClass='min-height'>
        <Routes>
          <Route path='/' element={<Home/>}/>
          <Route path='/login' element={<Login authLogin={authLogin} setAuthLogin={setAuthLogin}/>}/>
          <Route path='/Sobre' element={<Sobre/>}/>
          <Route path='/Contato' element={<Contato/>}/>
         
          {authLogin ? <>
          <Route path='/Projetos' element={<Projetos/>}/> 
          <Route path='/NovoProjeto' element={<NovoProjeto/>}/>
          <Route path='/Projeto/:id' element={<EditarProjeto/>}/>   
          </> : <>
          <Route path='/Projetos' element={<LoginMessage/>}/> 
          <Route path='/NovoProjeto' element={<LoginMessage/>}/>
          <Route path='/Projeto/:id' element={<LoginMessage/>}/> 
          </>}
           
        </Routes>
      </Container>
      <Footer/>
    </Router>
  );
}